export default {
	data() {
		return {
			order: {}
		}
	},
	onLoad(params) {
		let _this = this;
		_this.request({
			url: _this.http.orderDetail,
			data: {
				orderId: params.orderId
			},
			success: (res) => {
				if (res.code === '0') {
					_this.order = res.result;
				} else {
					uni.showToast({
						title: res.message,
						icon: 'none'
					});
				}
			}
		})
	},
	onPullDownRefresh() {
		let _this = this;
		_this.request({
			url: _this.http.orderDetail,
			data: {
				orderId: _this.order.orderId
			},
			success: (res) => {
				if (res.code === '0') {
					_this.order = res.result;
				} else {
					uni.showToast({
						title: res.message,
						icon: 'none'
					});
				}
				uni.stopPullDownRefresh();
			}
		}, false)
	},
	methods: {
		pickUp(type) {
			const _this = this;
			let latitude = '';
			let longitude = '';
			let name = '';
			switch (type) {
				case 1:
					latitude = _this.order.startLat;
					longitude = _this.order.startLong;
					name = _this.order.startPlace;
					break;
				case 2:
					latitude = _this.order.endLat;
					longitude = _this.order.endLong;
					name = _this.order.endPlace;
					break;
			}
			uni.openLocation({
				latitude: latitude,
				longitude: longitude,
				name: name,
				fail: function() {
					uni.showToast({
						icon: 'none',
						title: '打开失败，请完全关闭小程序之后再次进入或联系管理员。'
					})
				}
			});
		},
		inputCode(type) {
			uni.navigateTo({
				url: `/pages/code/code?type=${type}&orderId=${this.order.orderId}`
			})
		},
		complete(type) {
			let _this = this;
			let content = type === 1 ? '确定装货完成？' : '确定卸货完成？';
			uni.showModal({
				title: '提示',
				content: content,
				success: function(res) {
					if (res.confirm) {
						_this.request({
							url: _this.http.loadingComplete,
							data: {
								orderId: _this.order.orderId,
								type: type
							},
							success: (res)=>{
								if(res.code === "0"){
									uni.showToast({
										icon: "success",
										message: "操作成功"
									});
									_this.order.status = type === 1 ? 4 : 6;
								}else{
									uni.showToast({
										icon: "none",
										message: res.message
									})
								}
							}
						});
					}
				}
			});
		},
		end(){
			let _this = this;
			uni.showModal({
				title: '提示',
				content: "确定订单已完成，是否结算?",
				success: function(res) {
					if (res.confirm) {
						_this.request({
							url: _this.http.orderSettlement,
							data: {
								orderId: _this.order.orderId
							},
							success: (res)=>{
								if(res.code === "0"){
									uni.showToast({
										icon: "success",
										title: "操作成功"
									});
								}else{
									uni.showToast({
										icon: "none",
										title: res.message
									})
								}
							}
						});
					}
				}
			});
		}
	}
}
